html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor2 {
  width: 95%;
  max-width: 95rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 3.2rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.6rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: #349299;
}
@media (min-width: 768px) {
  .header.fijo {
    top: 0%;
    left: 0%;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.1rem 0.5rem 0.2rem;
    background-color: #A10035;
  }
}
.header h1 {
  color: #fff;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 2rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
  }
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
}
.navegacion-principal a {
  font-size: 1.8rem;
  text-align: center;
  margin-top: 1rem;
  position: relative;
  display: block;
  text-transform: uppercase;
  margin: 20px 0;
  padding: 10px 20px;
  text-decoration: none;
  color: black;
  font-family: sans-serif;
  font-weight: 600;
  transition: 0.5s;
  z-index: 1;
}
.navegacion-principal a:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-top: 2px solid #262626;
  border-bottom: 2px solid #262626;
  transform: scaleY(2);
  opacity: 0;
  transition: 0.3s;
}
.navegacion-principal a:after {
  content: "";
  position: absolute;
  top: 2px;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #262626;
  transform: scale(0);
  opacity: 0;
  transition: 0.3s;
  z-index: -1;
}
.navegacion-principal a:hover {
  color: #fff;
}
.navegacion-principal a:hover:before {
  transform: scaleY(1);
  opacity: 1;
}
.navegacion-principal a:hover:after {
  transform: scaleY(1);
  opacity: 1;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #F3F46D;
}

.video {
  position: relative;
  max-height: 45rem;
  min-height: 30rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  background: rgb(0, 35, 36);
  background: linear-gradient(63deg, rgba(0, 35, 36, 0.6905695384) 0%, rgba(0, 255, 105, 0.113538726) 100%);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.video video {
  display: block;
  width: auto;
}
@media (min-width: 768px) {
  .video video {
    width: 100%;
  }
}

.contenido-video {
  z-index: 1;
}
.contenido-video h2 {
  color: #fff;
  font-size: 4rem;
}
@media (min-width: 480px) {
  .contenido-video h2 {
    font-size: 5rem;
  }
}
.contenido-video p {
  font-size: 1.6rem;
  background-color: #A10035;
  display: inline-block;
  padding: 1rem 2rem;
  color: #fff;
  font-weight: 700;
  margin: 0;
}

.sobre-festival {
  padding: 9rem 0rem;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.contenido-festival {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contenido-festival h2 {
  margin-bottom: 2rem;
}
.contenido-festival .fecha {
  margin: 0;
  color: #A10035;
  display: inline-block;
  padding: 1rem 1rem;
  font-weight: 700;
}

.bg-cian {
  background-color: #3FA796;
}
.bg-cian li:nth-child(odd) {
  background-color: #318275;
}

.bg-naranja {
  background-color: #FEC260;
}
.bg-naranja li:nth-child(odd) {
  background-color: #feaf2d;
}

.lineup {
  background-color: #346099;
  padding: 5rem 0rem;
}
.lineup h3, .lineup .dia {
  text-align: center;
  color: #fff;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem auto;
}
.lineup h3 {
  font-weight: 700;
}

@media (min-width: 768px) {
  .escenario-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5;
  }
}

.escenario {
  width: auto;
  margin: 2rem;
}
.escenario p:after {
  content: "";
  display: block;
  width: 15rem;
  height: 15rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0rem auto;
}

.pop .nombre-escenario::after {
  background-image: url(../img/icono_edm.png);
}

.rock .nombre-escenario::after {
  background-image: url(../img/icono_rock.png);
}

.hiphop .nombre-escenario::after {
  background-image: url(../img/icono_hiphop.png);
}

.jazz .nombre-escenario::after {
  background-image: url(../img/icono_jazz.png);
}

.nombre-escenario {
  text-align: center;
  padding: 2rem;
}

.calendario {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 2rem;
  font-weight: 700;
}
.calendario li {
  padding: 2.5rem;
}
.calendario span {
  font-weight: 400;
}

.galeria {
  padding: 10rem 0rem;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
  }
}
@media (min-width: 992px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 1rem;
  }
}

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.7);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.btn-cerrar {
  display: grid;
  place-items: center;
  width: 4rem;
  height: 4rem;
  background-color: #fff;
  border-radius: 50%;
  font-weight: 700;
  color: #000;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.fijar-body {
  overflow: hidden;
}

.boletos {
  background-color: #a04e66;
  padding-bottom: 5rem;
}
.boletos h3 {
  text-align: center;
  margin: 0rem;
  color: white;
  padding-top: 5rem;
}

@media (min-width: 768px) {
  .boletos-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

.pase {
  text-align: center;
}
.pase h3 {
  color: white;
  font-weight: 400;
  margin: 2rem;
}

.boletos-style {
  margin: 0;
  padding: 0;
  list-style: none;
}
.boletos-style p {
  background-color: #fff;
  margin: 0;
  border: 1px solid gainsboro;
  padding: 1rem;
}

.pase-header {
  background-color: #993492;
  padding: 1rem;
}
.pase-header p {
  color: white;
  text-transform: uppercase;
  margin: 0;
}

.pase-precio {
  background-color: #346099;
  padding: 1rem;
}
.pase-precio p {
  color: white;
  font-weight: 500;
  margin: 0;
}

footer {
  padding: 1rem;
  color: #fff;
  background-color: red;
  text-align: center;
}